.container {
  width: 80vw;
  height: 50vh;
  margin: auto;
  border-radius: 10%;
  display: grid;
  grid-template-areas:
    "a a b"
    "a a c";
  grid-gap: 1vw;
}
.container div {
  background-size: cover;
  background-position: center center;
}
.big-image {
  background: url("https://media.istockphoto.com/photos/nothing-better-than-sliding-into-bed-with-a-good-read-picture-id929998552?b=1&k=20&m=929998552&s=170667a&w=0&h=GkJP5lHXoyVLYDIa86G6stcyEvoaAgN7xBFqgrc3QH8=");
  grid-area: a;
  border-radius: 10px;
  position: relative;
}

.text-on-big-img {
  position: absolute;
  top: 50%;
  left: 20%;
  overflow: hidden;
}

@media screen and (max-width: 700px) {
  .text-on-big-img h3 {
    font-size: 10px;
  }
  .text-on-big-img span {
    font-size: 10px;
  }
}

.img1 {
  position: relative;
  overflow: hidden;
}

.img2 {
  position: relative;
  overflow: hidden;
}

.text-on-small-img {
  position: absolute;
  top: 30%;
  left: 20%;
  font-size: 10px;
}

.img1 {
  background: url("https://media.istockphoto.com/photos/nothing-better-than-sliding-into-bed-with-a-good-read-picture-id929998552?b=1&k=20&m=929998552&s=170667a&w=0&h=GkJP5lHXoyVLYDIa86G6stcyEvoaAgN7xBFqgrc3QH8=");
  grid-area: b;
  border-radius: 5px;
}

.img2 {
  background: url("https://media.istockphoto.com/photos/nothing-better-than-sliding-into-bed-with-a-good-read-picture-id929998552?b=1&k=20&m=929998552&s=170667a&w=0&h=GkJP5lHXoyVLYDIa86G6stcyEvoaAgN7xBFqgrc3QH8=");
  grid-area: c;
  border-radius: 5px;
}

@media screen and (max-width: 700px) {
  .container {
    margin: auto;
    width: 98vw;
    height: 80vh;
    border-radius: 10%;
    display: grid;
    grid-template-areas:
      "a"
      "b"
      "c";
    grid-gap: 1vw;
  }
  .container div {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }
  .big-image {
    border-radius: 10px;
  }

  .img1 {
    border-radius: 10px;
  }

  .img2 {
    border-radius: 10px;
  }
}

